
import React from 'react';

const LogoMarquee = () => {
  // Generate 53 logo paths - distribute across 4 rows
  const allLogos = Array.from({ length: 53 }, (_, i) => `/colorful/${i + 1}.png`);
  
  // Split into 4 rows (13-13-13-14 distribution)
  const row1Logos = allLogos.slice(0, 13);
  const row2Logos = allLogos.slice(13, 26);
  const row3Logos = allLogos.slice(26, 39);
  const row4Logos = allLogos.slice(39, 53);

  const MarqueeRow = ({ logos, reverse = false }) => {
    return (
      <div className="relative w-full overflow-hidden my-6">
        {/* Left gradient shadow */}
        <div className="absolute left-0 top-0 bottom-0 w-32 bg-linear-to-r from-white via-white to-transparent z-10 pointer-events-none" />
        
        {/* Right gradient shadow */}
        <div className="absolute right-0 top-0 bottom-0 w-32 bg-linear-to-l from-white via-white to-transparent z-10 pointer-events-none" />
        
        <div className={`flex ${reverse ? 'animate-marquee-reverse' : 'animate-marquee'}`}>
          {/* Duplicate logos twice for seamless infinite loop */}
          {[1, 4].map((iteration) => (
            <div
              key={iteration}
              className="flex items-center gap-16 shrink-0"
            >
              {logos.map((logo, idx) => (
                <div
                  key={`${iteration}-${idx}`}
                  className="shrink-0 w-35 h-40 flex items-center justify-center group"
                >
                  <img
                    src={logo}
                    alt={`Logo ${idx + 1}`}
                    className="max-w-full max-h-full object-contain grayscale opacity-60 group-hover:grayscale-0 group-hover:opacity-100 transition-all duration-300 group-hover:scale-110"
                  />
                </div>
              ))}
            </div>
          ))}
        </div>

        <style jsx>{`
          @keyframes marquee {
            0% {
              transform: translateX(0);
            }
            100% {
              transform: translateX(-50%);
            }
          }

          @keyframes marqueeReverse {
            0% {
              transform: translateX(-50%);
            }
            100% {
              transform: translateX(0);
            }
          }

          .animate-marquee {
            animation: marquee 25s linear infinite;
          }

          .animate-marquee-reverse {
            animation: marqueeReverse 25s linear infinite;
          }

          .animate-marquee:hover,
          .animate-marquee-reverse:hover {
            animation-play-state: paused;
          }
        `}</style>
      </div>
    );
  };

  return (
    <div className="w-full py-8 overflow-hidden">
      {/* Row 1 - Right to Left - 13 logos */}
      <MarqueeRow logos={row1Logos} reverse={false} />
      
      {/* Row 2 - Left to Right - 13 logos */}
      <MarqueeRow logos={row2Logos} reverse={true} />
      
      {/* Row 3 - Right to Left - 13 logos */}
      <MarqueeRow logos={row3Logos} reverse={false} />
      
      {/* Row 4 - Left to Right - 14 logos */}
      <MarqueeRow logos={row4Logos} reverse={true} />
    </div>
  );
};

export default LogoMarquee;